header {
  color: #fff;
  position: fixed;
  top: 0;
  background-color: rgba(0, 0, 0, 0.45);
  width: 100%;
  padding: 1rem;
  z-index: 1; }
  header h1,
  header ul,
  header li,
  header a {
    color: inherit; }
  header h1 {
    display: inline-block;
    font-size: 1.6rem;
    font-family: 'Pacifico', cursive;
    margin: 0;
    font-weight: 100; }
  header a, header a:hover, header a:focus {
    text-decoration: none; }
  header ul {
    margin: 0;
    padding: .4rem 0;
    display: block;
    float: right; }
    header ul li {
      margin: 0 .9rem 0 0;
      padding: 0;
      display: inline-block;
      list-style: none; }
      header ul li:last-child {
        margin: 0; }
      header ul li.highlight a {
        background-color: #3b1879; }
        header ul li.highlight a:hover {
          background-color: #361459; }
      header ul li a {
        font-size: .75rem;
        padding: .8rem 1rem;
        -webkit-transition: background-color .45s;
        -o-transition: background-color .45s;
        transition: background-color .45s;
        border-radius: .16rem; }
        header ul li a:hover {
          background-color: rgba(0, 0, 0, 0.35); }

@media (min-width: 0) and (max-width: 968px) {
  header {
    height: auto; }
    header ul li {
      display: none; }
      header ul li.highlight {
        display: inline-block; } }



